@font-face {
  font-family: 'caviar_dreams';
  src: url('./assets/fonts/CaviarDreams-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;

}
@font-face {
  font-family: 'caviar_dreams';
  src: url('./assets/fonts/Caviar_Dreams_Bold-webfont.woff') format('woff');
  font-weight: bold;
  font-style: normal;

}
@font-face {
  font-family: 'caviar_dreams';
  src: url('./assets/fonts/CaviarDreams_Italic-webfont.woff') format('woff');
  font-weight: normal;
  font-style: italic;

}
@font-face {
  font-family: 'caviar_dreams';
  src: url('./assets/fonts/CaviarDreams_BoldItalic-webfont.woff') format('woff');
  font-weight: bold;
  font-style: italic;

}

@font-face {
  font-family: 'uppereastsideregular';
  src: url('./assets/fonts/UpperEastSide-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

* {
  scroll-behavior: smooth;
}

.App {
  min-height: 100vh;
  padding-top: 175px;


}

body {
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: "caviar_dreams","Source Sans Pro", sans-serif;
  color: white;
  background-image: url('./assets/pulpfiction.jpg');

  background-color: black;
  /* background-repeat: no-repeat; */
  scroll-behavior: smooth;
}

.btn {
  font-family:  "caviar_dreams","Source Sans Pro", sans-serif;
  font-weight: bold;
  font-size: 1em;
  border-radius: 10px;
}

@media (max-width: 800px) {
  .App {
    padding-top: 120px;
  }
}

